.header .header-1 {
  background: #fff;
  padding: 1.5rem 9%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .header-1 .logo {
  font-size: 2.5rem;
  font-weight: bolder;
  color: var(--black);
  text-decoration: none !important;
}
.header .header-1 .logo {
  border-bottom: none !important;
}
.header .header-1 .logo i {
  color: var(--green);
}

.header .header-1 .search-form {
  width: 50rem;
  height: 5rem;
  border: var(--border);
  overflow: hidden;
  background: #fff;
  display: flex;
  align-items: center;
  border-radius: 0.5rem;
}

.search-form input {
  font-size: 1.6rem;
  padding: 0 1.2rem;
  height: 100%;
  width: 100%;
  text-transform: none;
  color: var(--black);
}
.search-form .btn{
  background-color: white !important;
}
.search-form label {
  font-size: 2.5rem;
  padding-right: 1.5rem;
  color: var(--black);
  cursor: pointer;
}

 .search-form label:hover {
  color: var(--green);
}

.header .header-1 .icons{
  display: inline-block !important;
}
.header .header-1 .icons div,
.header .header-1 .icons .a {
  font-size: 2.5rem;
  margin-left: 1.5rem;
  color: var(--black);
  cursor: pointer;
  text-decoration: none !important;
}

.header .header-1 .icons div:hover,
.header .header-1 .icons .a:hover {
  color: var(--green);
}
.header .header-1 .icons .a.active {
  padding-bottom: 5px;
  border-bottom: 3px solid var(--green);
}

#search-btn {
  display: none;
}
/* user drop down */

